/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react'
import { atom, useRecoilState, useRecoilValueLoadable } from 'recoil'
import MuiButton from '@mui/material/Button'

import { useApi } from '../../data/api'
import { selectTaxa, useInvalidate, BOX_STATE_ID } from '../../data/selectors'
import { BoxTaxon, Box, Taxon } from '../../types'
import { Title } from '../Title'
import { reconcileBoxTaxonomy } from '../../data/calls'
import { useNotifications } from '../AppNotifications'
import { DataGrid, DataGridRecordCount } from '../DataGrid'
import { makeBoxTaxonomyColumnDefs } from './columnDefs'
import { AccordionPanel } from '../AccordionPanel'
import { downloadBoxTaxaReport } from './downloadBoxTaxaReport'
import { uniq } from 'lodash'
import { BoxTaxonomySelectField } from './BoxTaxonomySelectField'

export interface BoxTaxonomyProps {
  box: Box
  boxTaxa: BoxTaxon[]
}

const isExpandedState = atom<boolean>({
  key: 'BoxTaxonomy/isExpandedState',
  default: false,
})

export const BoxTaxonomy = ({ box, boxTaxa }: BoxTaxonomyProps): JSX.Element => {
  const api = useApi()
  const invalidate = useInvalidate()
  const notifications = useNotifications()

  const taxaLoadable = useRecoilValueLoadable(selectTaxa)

  const [fromTaxonomyId, setFromTaxonomyId] = useState<number | null>(null)
  const [toTaxonomyId, setToTaxonomyId] = useState<number | null>(null)
  const [isWorking, setIsWorking] = useState(false)
  const [taxa, setTaxa] = useState<Taxon[]>([])
  const hasTaxa = taxa.length > 0

  useEffect(() => {
    taxaLoadable.toPromise().then(setTaxa)
  }, [])

  const [isPanelExpanded, setIsPanelExpanded] = useRecoilState(isExpandedState)

  const isSameIdError = !!fromTaxonomyId && !!toTaxonomyId && fromTaxonomyId === toTaxonomyId

  const boxTaxonIds = boxTaxa.map(({ id }) => id)
  const addedTaxonOptions = taxa
    .filter((taxon) => boxTaxonIds.includes(taxon.id))
    .map((taxon) => ({ ...taxon, status: 'in' as const }))
  const availableTaxonOptions = taxa
    .filter((taxon) => !boxTaxonIds.includes(taxon.id))
    .map((taxon) => ({ ...taxon, status: 'notIn' as const }))

  const fromBoxTaxon = boxTaxa.find(({ id }) => id === fromTaxonomyId)
  const toTaxon = taxa.find(({ id }) => id === toTaxonomyId)
  const toBoxTaxon = boxTaxa.find(({ id }) => id === toTaxonomyId)

  const handleReconcile = async () => {
    if (!fromTaxonomyId || !toTaxonomyId) throw new Error('Missing taxonomid IDs')
    setIsWorking(true)
    try {
      await reconcileBoxTaxonomy(api, invalidate)({ boxId: box.id, fromTaxonomyId, toTaxonomyId })
      setFromTaxonomyId(null)
      setToTaxonomyId(null)
      setIsWorking(false)
    } catch (err) {
      notifications.error(`Failed to reconcile taxonomy: ${err}`)
    }
  }

  const taxonomistNames = boxTaxa.reduce<string[]>((acc, record) => {
    return uniq([...acc, ...Object.keys(record.taxonomistCounts)])
  }, [])
  const rows = boxTaxa
  const columnDefs = makeBoxTaxonomyColumnDefs(taxonomistNames)

  const canReconcileTaxa = box.boxStateId === BOX_STATE_ID.active

  return (
    <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      <div css={{ margin: '16px', display: 'flex', alignItems: 'center' }}>
        <Title>
          Box {box.id} Taxonomy{' '}
          <Title.Subtitle>
            ({box.boxState}, {box.sampleCount} samples)
          </Title.Subtitle>
        </Title>
        <div css={{ marginLeft: 'auto' }}>
          <MuiButton
            variant="outlined"
            onClick={async () => {
              try {
                const cancelNotification = notifications.info('Still creating report...', 1000)
                await downloadBoxTaxaReport({
                  box,
                  boxTaxa,
                  filename: `Box Taxonomy Report for Box ID ${box.id}`,
                })
                cancelNotification()
              } catch (err) {
                notifications.error(`Failed to create box taxonomy report: ${err}`)
              }
            }}
          >
            Download Report
          </MuiButton>
        </div>
      </div>
      <AccordionPanel value={isPanelExpanded} onChange={setIsPanelExpanded} summary={<span>Reconcile Taxa</span>}>
        {canReconcileTaxa ? (
          <div
            css={{
              display: 'grid',
              gap: '1em',
              paddingTop: '1em',
              gridTemplateColumns: '1fr 1fr',
              gridTemplateAreas: `
            "fromId      toId"
            "fromMessage toMessage"
            ".           button"
            `,
            }}
          >
            <div css={{ gridArea: 'fromId' }}>
              <BoxTaxonomySelectField
                label="Reconcile From"
                options={addedTaxonOptions}
                value={fromTaxonomyId}
                onChange={setFromTaxonomyId}
                disabled={isWorking}
                skeleton={!hasTaxa}
              />
            </div>
            <div css={{ gridArea: 'fromMessage' }}>
              {fromBoxTaxon ? (
                <span>
                  Box currently contains {fromBoxTaxon.totalCount} <em>{fromBoxTaxon.name}</em> records in{' '}
                  {fromBoxTaxon.sampleCount} samples.
                </span>
              ) : (
                <span>
                  <em>select a taxon</em>
                </span>
              )}
            </div>
            <div css={{ gridArea: 'toId' }}>
              <BoxTaxonomySelectField
                label="Reconcile To"
                options={[...addedTaxonOptions, ...availableTaxonOptions]}
                value={toTaxonomyId}
                onChange={setToTaxonomyId}
                disabled={isWorking}
                skeleton={!hasTaxa}
              />
            </div>
            <div css={{ gridArea: 'toMessage' }}>
              {(() => {
                if (isSameIdError) return <span css={{ color: 'red' }}>Cannot reconcile to the same taxon.</span>
                if (toBoxTaxon)
                  return (
                    <span>
                      Box currently contains {toBoxTaxon.totalCount} <em>{toBoxTaxon.name}</em> records in{' '}
                      {toBoxTaxon.sampleCount} samples.
                    </span>
                  )
                if (toTaxon)
                  return (
                    <span>
                      Box does not yet contain any <em>{toTaxon?.name}</em> records.
                    </span>
                  )
                return (
                  <span>
                    <em>select a taxon</em>
                  </span>
                )
              })()}
            </div>
            <div css={{ gridArea: 'button', display: 'flex', justifyContent: 'end' }}>
              <MuiButton
                variant="contained"
                disabled={!fromTaxonomyId || !toTaxonomyId || isSameIdError || isWorking}
                onClick={handleReconcile}
              >
                Reconcile Taxa
              </MuiButton>
            </div>
          </div>
        ) : (
          <div>Box must be active to perform taxa reconciliation.</div>
        )}
      </AccordionPanel>
      <div css={{ minHeight: '1rem', display: 'flex' }}>
        <DataGridRecordCount css={{ marginLeft: 'auto' }} total={rows.length} visible={rows.length} descriptor="taxa" />
      </div>
      <div css={{ flex: 1 }}>
        <DataGrid<BoxTaxon> columnDefs={columnDefs} rows={rows} />
      </div>
    </div>
  )
}
