/** @jsxImportSource @emotion/react */
import React from 'react'

import { Chip } from '../Chip'
import { Filter } from './types'

export interface FilterChipsProps<T> {
  className?: string
  filters: Filter<T>[]
  joinLabel?: 'and' | 'or'
}

const DEFAULT_CHIP_COLOR = 'lightblue'

export const FilterChips = <T,>({ className, filters, joinLabel }: FilterChipsProps<T>): JSX.Element => {
  return (
    <div
      className={className}
      css={{ flex: 1, display: 'flex', gap: '0.5em', alignItems: 'center', fontSize: '0.8125rem' }}
    >
      {filters.length > 0 ? (
        filters.map(({ id, label, textColor, chipColor = DEFAULT_CHIP_COLOR, onDelete }, index) => (
          <React.Fragment key={id}>
            {joinLabel && !!index && <span css={{ fontStyle: 'italic', opacity: 0.6 }}> {joinLabel} </span>}
            <Chip label={label} textColor={textColor} chipColor={chipColor} onDelete={onDelete} />
          </React.Fragment>
        ))
      ) : (
        <Chip label="No filters" />
      )}
    </div>
  )
}
